.nav {
  
  width: 100%;
  height: 75px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-line-pack: start;
      align-content: flex-start;
  position: absolute;
  top: 0;
  z-index: 1;
  color: white;
  text-shadow: 1px 1px 4px #000000;
  font-size: 24px;
  text-align: center;

 background: -webkit-gradient(
    linear,
    left top, left bottom,
    from(rgba(0, 0, 0, 0.5)),
     color-stop(rgba(0, 0, 0, 0.2)),
    to(rgba(255, 0, 0, 0))
  );

 background: -webkit-linear-gradient(
    top,
    rgba(0, 0, 0, 0.5),
     rgba(0, 0, 0, 0.2),
    rgba(255, 0, 0, 0)
  );

 background: -o-linear-gradient(
    top,
    rgba(0, 0, 0, 0.5),
     rgba(0, 0, 0, 0.2),
    rgba(255, 0, 0, 0)
  );

 background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.5),
     rgba(0, 0, 0, 0.2),
    rgba(255, 0, 0, 0)
  );
  -ms-flex-align: center;
      align-items: center;
}
.nav a {
  text-decoration: none;
  color:white;
  text-shadow: 1px 1px 4px #000000;
  font-size: 24px;

}
h1{
  font-size: 36px;
}


